import {
  Pie
} from 'vue-chartjs'

export default {
  extends: Pie,
  props: ['data', 'options'],
  mounted () {
    this.renderPieChart()
  },
  computed: {
    chartData: function () {
      return this.data
    }
  },
  methods: {
    renderPieChart: function () {
      this.renderChart({
        labels: this.chartData.map(entry => entry.CATEGORI),
        datasets: [{
          label: 'Jumlah Pasien',
          backgroundColor: ['#FF0000', '#FF0033', '#FF3300', '#FF0066', '#FF6600', '#FF6633', '#FF9900', '#FF9933', '#FFCC00', '#FFCC33'],
          data: this.chartData.map(entry => entry.jml)
        }]
      },
      { responsive: true, maintainAspectRatio: false }
      )
    }
  },
  watch: {
    data: function () {
      this.$data._chart.destroy()
      this.renderPieChart()
    }
  }
}



