<template>
  <div>
    <div>
      <strong>
        <h4 style="text-align:left">{{ title }}</h4>
      </strong>
    </div>
    <div class="kolom6 kolom12-m judul maLeft maLeft-m">
      <i class="fa fa-chart-bar"></i> Grafik Pengunjung
    </div>

    <div class="kolom6 kolom12-m maLeft maLeft-m dateBorder">
      <div class="TopDate">
        <date-pick
          v-model="tgl"
          :inputAttributes="{readonly: true}"
          :isDateDisabled="isFutureDate"
        ></date-pick>
      </div>
      <div class="TopDate">
        <date-pick
          v-model="tgl2"
          :inputAttributes="{readonly: true}"
          :isDateDisabled="isFutureDate"
        ></date-pick>
      </div>
      <!-- <div class="kolom3 TopDate LeftDate"><input v-model="tgl" type="number" class="form-control"></div>
      <div class="kolom3 TopDate LeftDate"><input v-model="tgl2" type="number" class="form-control"></div>-->
    </div>
    <line-chart
      v-if="result.length > 0"
      :data="result"
      :options="{responsive: true, maintainAspectRatio: false}"
      class="kolom6 kolom12-m chart chartBorder clear maLeft maLeft-m"
    ></line-chart>
    <line-chart
      v-if="result2.length > 0"
      :data="result2"
      :options="{responsive: true, maintainAspectRatio: false}"
      class="kolom6 kolom12-m chart chartBorder clear maLeft maLeft-m"
    ></line-chart>
    <div v-else class="kolom12">
      <line-chart
        v-if="loaded"
        :data="hasil"
        :options="{responsive: true, maintainAspectRatio: false}"
        class="kolom6 kolom12-m chart chartBorder clear maLeft maLeft-m"
      ></line-chart>
    </div>
    <div class="kolom12 kolom12-m judul Top">
      <i class="fa fa-table"></i> Detail Pengunjung
    </div>
    <div v-if="result.length > 0" class="kolom12 Border1" style="overflow-x:auto;">
      <table class="table table-border">
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">No</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Grup Unit</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Nama Grup</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" colspan="2">Baru</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" colspan="2">Lama</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Jumlah Pasien</th>
        </tr>
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Laki-Laki</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Perempuan</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Laki-Laki</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Perempuan</th>
        </tr>
        <tr v-for="(row, no) in result" :key="row.NMGRUNIT">
          <td style="text-align:left; padding-left: 5px;">{{no+1}}</td>
          <td style="text-align:left; padding-right:5px; padding-left:5px">{{row.NMGRUNIT}}</td>
          <td style="text-align:left; padding-right:5px; padding-left:5px">{{row.NMGUNIT}}</td>
          <td style="text-align:center;">{{row.JMLLBR}}</td>
          <td style="text-align:center;">{{row.JMLPBR}}</td>
          <td style="text-align:center;">{{row.JMLLLM}}</td>
          <td style="text-align:center;">{{row.JMLPLM}}</td>
          <td style="text-align:center;">{{row.JMLPAS}}</td>
        </tr>
      </table>
    </div>
    <div v-else-if="result2.length > 0" class="kolom12 Border1" style="overflow-x:auto;">
      <table class="table table-border">
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">No</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Grup Unit</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Nama Grup</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" colspan="2">Baru</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" colspan="2">Lama</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Jumlah Pasien</th>
        </tr>
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Laki-Laki</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Perempuan</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Laki-Laki</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Perempuan</th>
        </tr>
        <tr v-for="(row, no) in result2" :key="row.NMGRUNIT">
          <td style="text-align:left; padding-left: 5px;">{{no+1}}</td>
          <td style="text-align:left; padding-right:5px; padding-left:5px">{{row.NMGRUNIT}}</td>
          <td style="text-align:left; padding-right:5px; padding-left:5px">{{row.NMGUNIT}}</td>
          <td style="text-align:center;">{{row.JMLLBR}}</td>
          <td style="text-align:center;">{{row.JMLPBR}}</td>
          <td style="text-align:center;">{{row.JMLLLM}}</td>
          <td style="text-align:center;">{{row.JMLPLM}}</td>
          <td style="text-align:center;">{{row.JMLPAS}}</td>
        </tr>
      </table>
    </div>
    <div v-else class="kolom12 Border1" style="overflow-x:auto;">
      <table class="table table-border">
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">No</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Grup Unit</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Nama Grup</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" colspan="2">Baru</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" colspan="2">Lama</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px" rowspan="2">Jumlah Pasien</th>
        </tr>
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Laki-Laki</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Perempuan</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Laki-Laki</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">Perempuan</th>
        </tr>
        <tr v-for="(row, no) in hasil" :key="row.NMGRUNIT">
          <td style="text-align:left; padding-left: 5px;">{{no+1}}</td>
          <td style="text-align:left; padding-right:5px; padding-left:5px">{{row.NMGRUNIT}}</td>
          <td style="text-align:left; padding-right:5px; padding-left:5px">{{row.NMGUNIT}}</td>
          <td style="text-align:center;">{{row.JMLLBR}}</td>
          <td style="text-align:center;">{{row.JMLPBR}}</td>
          <td style="text-align:center;">{{row.JMLLLM}}</td>
          <td style="text-align:center;">{{row.JMLPLM}}</td>
          <td style="text-align:center;">{{row.JMLPAS}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import DatePick from "vue-date-pick";
import LineChart from "@/js/RekamMedis/pengunjungLine.js";

const url = "http://10.0.14.198:8000/api/pengunjung";
export default {
  components: {
    LineChart,
    DatePick
  },
  data() {
    return {
      tgl: null,
      tgl2: null,
      title: "Pengunjung",
      hasil: [],
      result: [],
      result2: [],
      loaded: false
    };
  },
  watch: {
    tgl(after, before) {
      this.oneDate();
    },
    tgl(after, before) {
      this.twoDate();
    },
    tgl2(after, before) {
      this.twoDate();
    }
  },
  mounted() {
    this.requestData();
    this.oneDate();
    this.twoDate();
  },
  methods: {
    requestData: function() {
      this.axios.get(url).then(response => {
        (this.hasil = response.data), (this.loaded = true);
      });
    },
    oneDate() {
      this.axios
        .get(url + "/" + this.tgl)
        .then(response => (this.result = response.data))
        .catch(error => {});
    },
    twoDate() {
      this.axios
        .get(url + "/" + this.tgl + "/" + this.tgl2)
        .then(response => (this.result2 = response.data))
        .catch(error => {});
    },
    isFutureDate(date) {
      const currentDate = new Date();
      return date > currentDate;
    }
  }
};
</script>

<style>
</style>



