import {
  Pie,
  mixins
} from 'vue-chartjs'

export default {
  extends: Pie,
  mixins: [mixins.reactiveProp],
  data () {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    this.renderChart({
      labels: this.chartData.map(entry => entry.CATEGORI),
      datasets: [{
        label: 'Jumlah Pasien',
        backgroundColor: ['#FF1700', '#FF4500', '#FF8300', '#FFB900', '#FCFF00', '#DDFF00', '#BAFF00', '#94FF00', '#56FF00', '#11FF00', '#00FF67', '#00FFB8', '#00FFF5', '#00D3FF'],
        data: this.chartData.map(entry => entry.jml)
      }]
    }, this.options)
  }
}



