<template>
  <div>
    <div>
      <strong>
        <h4 style="text-align:left">{{ title }}</h4>
      </strong>
    </div>
    <div class="kolom6 kolom12-m judul maLeft maLeft-m">
      <i class="fa fa-chart-bar"></i> Grafik Laporan Pendapatan Tunai
    </div>
    <div class="kolom6 kolom12-m maLeft maLeft-m dateBorder">
      <!-- <form action="/PendapatanTunai"> -->
      <div class="TopDate">
        <date-pick v-model="tgl" :inputAttributes="{readonly: true}" :isDateDisabled="isFutureDate"></date-pick>
      </div>
      <div class="TopDate">
        <date-pick
          v-model="tgl2"
          :inputAttributes="{readonly: true}"
          :isDateDisabled="isFutureDate"
        ></date-pick>
      </div>
      <!-- <div class="kolom3 TopDate LeftDate"><input v-model="tgl" type="number" class="form-control"></div>
      <div class="kolom3 TopDate LeftDate"><input v-model="tgl2" type="number" class="form-control"></div>-->
      <!-- <button type="submit" class="btn btn-primary TopDate kolom4 kolom4-m" style="margin-left: 5px;">Show <i class="fa fa-arrow-circle-right"></i></button>
      </form>-->
    </div>
    <bar-chart
      v-if="result.length > 0"
      :data="result"
      class="kolom6 kolom12-m chart chartBorder clear maLeft maLeft-m"
    ></bar-chart>
    <bar-chart
      v-if="result2.length > 0"
      :data="result2"
      class="kolom6 kolom12-m chart chartBorder clear maLeft maLeft-m"
    ></bar-chart>
    <div v-else class="kolom12">
      <bar-chart
        v-if="loaded"
        :data="hasil"
        class="kolom6 kolom12-m chart chartBorder clear maLeft maLeft-m"
      ></bar-chart>
    </div>
    <div class="kolom12 judul Top">
      <i class="fa fa-table"></i> Detail Pendapatan Tunai
    </div>
    <div v-if="result.length > 0" class="kolom12 Border1" style="overflow-x:auto;">
      <table class="table table-border">
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px">No</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">NAMA GRUP</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">JUMLAH BAYAR</th>
          <th
            style="text-align:center; padding-right:5px; padding-left:5px"
          >RAWAT JALAN</th>
          <th
            style="text-align:center; padding-right:5px; padding-left:5px"
          >RAWAT DARURAT</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT INAP</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT PENUNJANG MEDIS</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT PENUNJANG NON MEDIS</th>
        </tr>
        <tr v-for="(row,no) in result" :key="row.NMGTRFPT">
          <td style="text-align:left; padding-left: 5px;">{{no+1}}</td>
          <td style="text-align:left; padding-left: 5px;">{{ row.NMGTRFPT }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYAR | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRJ | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRD | numFormat}}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRI | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARPM | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARNM | numFormat}}</td>
        </tr>
      </table>
    </div>
    <div v-else-if="result2.length > 0" class="kolom12 Border1" style="overflow-x:auto;">
      <table class="table table-border">
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px">No</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">NAMA GRUP</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">JUMLAH BAYAR</th>
          <th
            style="text-align:center; padding-right:5px; padding-left:5px"
          >RAWAT JALAN</th>
          <th
            style="text-align:center; padding-right:5px; padding-left:5px"
          >RAWAT DARURAT</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT INAP</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT PENUNJANG MEDIS</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT PENUNJANG NON MEDIS</th>
        </tr>
        <tr v-for="(row,no) in result2" :key="row.NMGTRFPT">
          <td style="text-align:left; padding-left: 5px;">{{no+1}}</td>
          <td style="text-align:left; padding-left: 5px;">{{ row.NMGTRFPT }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYAR | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRJ | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRD | numFormat}}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRI | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARPM | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARNM | numFormat}}</td>
        </tr>
      </table>
    </div>
    <div v-else class="kolom12 Border1" style="overflow-x:auto;">
      <table class="table table-border">
        <tr>
          <th style="text-align:center; padding-right:5px; padding-left:5px">No</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">NAMA GRUP</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">JUMLAH BAYAR</th>
          <th
            style="text-align:center; padding-right:5px; padding-left:5px"
          >RAWAT JALAN</th>
          <th
            style="text-align:center; padding-right:5px; padding-left:5px"
          >RAWAT DARURAT</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT INAP</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT PENUNJANG MEDIS</th>
          <th style="text-align:center; padding-right:5px; padding-left:5px">RAWAT PENUNJANG NON MEDIS</th>
        </tr>
        <tr v-for="(row,no) in hasil" :key="row.NMGTRFPT">
          <td style="text-align:left; padding-left: 5px;">{{no+1}}</td>
          <td style="text-align:left; padding-left: 5px;">{{ row.NMGTRFPT }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYAR | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRJ | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRD | numFormat}}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARRI | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARPM | numFormat }}</td>
          <td style="text-align:right; padding-right: 5px;">{{ row.JMLBAYARNM | numFormat}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
const url = "http://10.0.14.198:8000/api/pendpTunai"; // dokumen ada di API folder

import DatePick from "vue-date-pick";
import DatePicker from "vue2-datepicker";
import BarChart from "@/js/Pendapatan/pendpTunai.js";
export default {
  components: {
    BarChart,
    DatePick
  },
  data() {
    return {
      tgl: null,
      tgl2: null,
      title: "Pendapatan Tunai",
      hasil: [],
      result: [],
      result2: [],
      loaded: false
    };
  },
  watch: {
    tgl(after, before) {
      this.oneDate();
    },
    tgl(after, before) {
      this.twoDate();
    },
    tgl2(after, before) {
      this.twoDate();
    }
  },
  mounted() {
    this.requestData();
    this.oneDate();
    this.twoDate();
  },
  methods: {
    requestData() {
      this.axios.get(url).then(response => {
        (this.hasil = response.data), (this.loaded = true);
      });
    },
    oneDate() {
      this.axios
        .get(url + "/" + this.tgl)
        .then(response => (this.result = response.data))
        .catch(error => {});
    },
    twoDate() {
      this.axios
        .get(url + "/" + this.tgl + "/" + this.tgl2)
        .then(response => (this.result2 = response.data))
        .catch(error => {});
    },
    isFutureDate(date) {
      const currentDate = new Date();
      return date > currentDate;
    }
  }
};
</script>

<style>
</style>



